body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /* background: #34495e; */
}
.box{
  width: 300px;
  padding: 65px;
  padding-top: 10px;
  padding-bottom: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background: #191919;
  text-align: center;
  border-radius: 40px;
}
.box h1{
  color: white;
  text-transform: uppercase;
  font-weight: 500;
}

.box input[type = "text"],.box input[type = "password"]{
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #3498db;
  padding: 14px 10px;
  width: 200px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
}
.box input[type = "text"]:focus,.box input[type = "password"]:focus{
  width: 280px;
  border-color: #2ecc71;
}
.box button[type = "submit"]{
  border:0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #2ecc71;
  padding: 14px 40px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
  cursor: pointer;
}
.box button[type = "submit"]:hover{
  background: #2ecc71;
}

form.shake {
  animation: shake 1s infinite;
}

@keyframes shake {
  0% {
    transform: translate(-50%,-50%);
  }
  20% {
    transform: translate(-40%,-50%)
  }
  40% {
    transform: translate(-60%,-50%)
  }
  60% {
    transform: translate(-40%,-50%)
  }
  80% {
    transform: translate(-60%,-50%)
  }
  100% {
    transform: translate(-50%,-50%)
  }
}



