table.table {
  border-spacing: 1;
  border-collapse: collapse;
  background: white;
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  position: relative;
}
table.table * {
  position: relative;
}
table.table td, table.table th {
  padding-left: 8px;
}
table.table thead tr {
  height: 46px;
  background: rgba(235,235,235,1);
  box-shadow: 1px 1px 2px rgba(25,25,25,.16);
}
table.table tbody tr {
  height: 46px;
}
table.table tbody tr:last-child {
  border: 0;
}
table.table td, table.table th {
  text-align: left;
}

table.table > thead th {
  font-size: 14px;
  font-weight: bold;
  color: rgb(25,25,25);
  line-height: 1.2;
}

.table tbody tr:nth-child(even) {
  background-color: #f5f5f5;
}

.table tbody tr {
  font-size: 15px;
  color: #808080;
  line-height: 1.2;
  font-weight: unset;
}

