.checkbox {
  position: relative;
  margin: 18px auto;
}
.checkbox span {
  margin-left: 5px;
  font-size: 12px;
}
.checkbox label {
  width: 18px;
  height: 18px;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 4px;
  border: 2px solid #3498db;
}
.checkbox label:after {
  content: '';
  width: 7px;
  height: 4px;
  position: absolute;
  top: 4px;
  left: 4px;
  border: 2px solid rgb(46,204,113);
  border-top: none;
  border-right: none;
  background: transparent;
  opacity: 0;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.checkbox label:hover::after {
  opacity: 0.3;
}
.checkbox input[type=checkbox] {
  visibility: hidden;
}
.checkbox input[type=checkbox]:checked + label:after {
  opacity: 1;
}


